import React, { Component } from "react";
import screenfull from "screenfull";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mainImageNumber: 1,
      secondImageNumber: 1,
      thirdImageNumber: 1,
      fourthImageNumber: 1,
      fifthImageNumber: 1,
      sixthImageNumber: 1,
    };
    this.runCount = 0;
    this.updateInterval = null; // For the update interval
  }

  componentDidMount() {
    this.startUpdates(); // Start updating content
  }

  componentWillUnmount() {
    clearInterval(this.updateInterval); // Clear the interval on unmount
  }

  startUpdates() {
    this.updateContent(); // Initial update
    this.updateInterval = setInterval(() => {
      this.updateContent(); // Continuously update content
    }, 7000); // Update every 7 seconds (you can adjust this interval)
  }


  getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min) + min);
  }

  // Function to generate a random scale value between 1 and 1.8
  getRandomScale() {
    const scale = Math.random() * (1.8 - 1) + 1; // Range from 1 to 1.8
    return scale;
  }

  // Function to randomly update the image
  updateImage(imageKey) {
    return new Promise((resolve) => {
      const imageNumber = this.getRandomInt(1, 73); // Random image number between 1 and 73
      const duration = Math.floor(Math.random() * 800); // Random duration for the update
      setTimeout(() => {
        this.setState({ [imageKey]: imageNumber }, resolve);
      }, duration);
    });
  }

  // Function to update the text in a random manner
  async updateContent() {
    // Randomize the order of image updates
    const imageKeys = [
      "mainImageNumber",
      "secondImageNumber",
      "thirdImageNumber",
      "fourthImageNumber",
      "fifthImageNumber",
      "sixthImageNumber",
    ];

    // Shuffle the image keys array to randomize the update order
    const shuffledKeys = imageKeys.sort(() => Math.random() - 0.5);

    // Randomly update images
    for (const key of shuffledKeys) {
      await this.updateImage(key); // Update each image independently
    }
  }

  goFullScreen() {
    screenfull.request();
  }

  render() {
    const { mainImageNumber, secondImageNumber, thirdImageNumber, fourthImageNumber, fifthImageNumber, sixthImageNumber } = this.state;
    const requestImageFile = require.context("./images", true, /.gif$/);

    // Generate random scales for each image independently
    const mainImageScale = this.getRandomScale();
    const secondImageScale = this.getRandomScale();
    const thirdImageScale = this.getRandomScale();
    const fourthImageScale = this.getRandomScale();
    const fifthImageScale = this.getRandomScale();
    const sixthImageScale = this.getRandomScale();

    return (
      <div className="App">
        {/* Removed .theWords div as requested */}

        {/* Image divs with onClick to go fullscreen */}
        <div className="theArt" onClick={() => this.goFullScreen()}>
          <img
            src={requestImageFile(`./${mainImageNumber}.gif`)}
            alt="art1"
            style={{ transform: `scale(${mainImageScale})` }} // Apply random scale
          />
        </div>

        <div className="theArt2" onClick={() => this.goFullScreen()}>
          <img
            src={requestImageFile(`./${secondImageNumber}.gif`)}
            alt="art2"
            style={{ transform: `scale(${secondImageScale})` }} // Apply random scale
          />
        </div>

        <div className="theArt3" onClick={() => this.goFullScreen()}>
          <img
            src={requestImageFile(`./${thirdImageNumber}.gif`)}
            alt="art3"
            style={{ transform: `scale(${thirdImageScale})` }} // Apply random scale
          />
        </div>

        <div className="theArt4" onClick={() => this.goFullScreen()}>
          <img
            src={requestImageFile(`./${fourthImageNumber}.gif`)}
            alt="art4"
            style={{ transform: `scale(${fourthImageScale})` }} // Apply random scale
          />
        </div>

        <div className="theArt5" onClick={() => this.goFullScreen()}>
          <img
            src={requestImageFile(`./${fifthImageNumber}.gif`)}
            alt="art5"
            style={{ transform: `scale(${fifthImageScale})` }} // Apply random scale
          />
        </div>

        <div className="theArt6" onClick={() => this.goFullScreen()}>
          <img
            src={requestImageFile(`./${sixthImageNumber}.gif`)}
            alt="art6"
            style={{ transform: `scale(${sixthImageScale})` }} // Apply random scale
          />
        </div>
      </div>
    );
  }
}

export default App;
